// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'expose-loader?jQuery!jquery' // eslint-disable-line
import 'expose-loader?$!jquery' // eslint-disable-line
import Vue from 'vue';

import BootstrapVue from 'bootstrap-vue';
import VCalendar from 'v-calendar';
import * as VueGoogleMaps from 'vue2-google-maps';
import VueTextareaAutosize from 'vue-textarea-autosize';
import trumbowyg from 'vue-trumbowyg';
import mavonEditor from 'mavon-editor';
import { VueMaskDirective } from 'v-mask';
import VueSlider from 'vue-slider-component';
import VeeValidate from 'vee-validate';
import VueFormWizard from 'vue-form-wizard';
import { ClientTable } from 'vue-tables-2';
import Trend from 'vuetrend';
import i18n from './plugins/i18n';
import VueResource from 'vue-resource';

import store from './store/index';
import router from './Routes'; 
// import { sync } from 'vuex-router-sync'
// sync(store, routes)
import App from './App';
import axios from 'axios';


axios.defaults.baseURL = 'http://localhost:5000/api/'
 //axios.defaults.headers.common['Authorization'] = 'fasfdsa'
axios.defaults.headers.get['Accepts'] = 'application/json'


Vue.use(BootstrapVue);

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyB7OXmzfQYua_1LEhRdqsoYzyJOPh9hGLg',
  },
});

Vue.use(VCalendar, {
  firstDayOfWeek: 2,  // Monday
});

Vue.use(VueTextareaAutosize);
Vue.use(trumbowyg);
Vue.use(mavonEditor);
Vue.directive('mask', VueMaskDirective);
Vue.component('VueSlider', VueSlider);
Vue.use(VeeValidate, { fieldsBagName: 'fieldsbag', i18n });
Vue.use(VueFormWizard);

Vue.use(ClientTable, { theme: 'bootstrap4' });
Vue.use(Trend);

Vue.config.productionTip = false;

Vue.use(VueResource);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  // components:{
  //   Employee :() => import('./pages/Forms/Employee/Employee.vue'),
  //   Customer :() => import('./pages/Forms/Customer/Customer.vue'),
  //   Patient: () => import('./pages/Forms/Patient/Patient.vue'), 

  // },
  i18n,
  router,
  store,
  render: h => h(App),
});
