router.beforeEach((to, from, next) => {
  const lang = to.params.lang;
  let l = i18n
  // debugger;

  if (!['en', 'pl'].includes(lang)) return next('en/login')
  if (window.localStorage.getItem('authenticated') !== 'true' ) {
    if(to.name === `Register`){

    }
    else
    if(to.name !== `Login`){
    this.$router.replace({ name: 'Login', params: { lang: l.locale } });
    } 
  }
  //const yy = Vue.i18n.locale;

  if (l.locale !== lang) {
    l.locale = lang;
  }

  return next();
})
export default router


