const starDate = '1970-01-01T00:00:00';

const state = {
    theCache: {
        'employee': [],
        'customer': [],
        'animal': [],
        'equipment': [],
        'assortment': [],
        'servic': [],
        'equipmentType' : [],
        'assortmentType': [],
        'serviceType': [],
        'DDL/EmployeeRole': [],
        'DDL/Plec': [],
        'DDL/AnimalRace': [],
        'DDL/AnimalSpecies': [],
        'DDL/Province': [],
        'DDL/Storage': [],
        'DDL/Country': [],
        'DDL/Tax': [],

    },
    cacheLastUpdate: {
        'employee': starDate,
        'customer': starDate,
        'animal': starDate,
        'equipment': starDate,
        'assortment': starDate,
        'servic': starDate,
        'equipmentType': starDate,
        'assortmentType': starDate,
        'serviceType': starDate,
        'DDL/EmployeeRole': starDate,
        'DDL/Plec': starDate,
        'DDL/AnimalRace': starDate,
        'DDL/AnimalSpecies': starDate,
        'DDL/Province': starDate,
        'DDL/Storage': starDate,
        'DDL/Country': starDate,
        'DDL/Tax': starDate,
    },
    activeOnArrival: {
        list: '',
        id: '',
    },
    styleTheme: 'default', // lightTheme 
};

const getters = {
    
    cacheExists: (state) => (name) => { 
        if(state.theCache[name] === undefined || state.cacheLastUpdate[name] == starDate) {
            console.log('Cache(' + name + ') not created');
            return false;
        }
        else return true;
    },  
    cacheItemsCount: (state) => (name) => {
        if (state.theCache[name] === undefined)
            return -1; 
        // if (state.theCache[cacheName] !== null)
        return state.theCache[name].length;
    },
    getCache: (state) => (name) => {
        console.log('%cUsing cache(' + name + ')', 'color:lime;');
        return state.theCache[name];
    },
    cacheLastUpdate: (state) => (name) => {
        return state.cacheLastUpdate[name];
    },
    cacheGetRowById: (state) => (payload) => {
        var index = state.theCache[payload.name].findIndex(x => x.id === payload.id); // not supported in IE
        console.log('From cache: ' + payload.name + '/' + payload.id);
        return state.theCache[payload.name][index];
    },
    getActiveOnArrival: (state) => {
        return state.activeOnArrival;
    },
    getStyleTheme: (state) => {
        return state.styleTheme;
    },

};

const mutations = {
    resetCache: (state, cacheName) => {
        state.theCache[cacheName] = [];
        state.cacheLastUpdate[cacheName] = starDate;
    },
    fillCache: (state, payload) => {
        state.theCache[payload.name] = payload.data;
        // state.cacheLastUpdate[payload.name] = payload.updateDate;  
    },
    addToCache: (state, payload) => {
        state.theCache[payload.name].push(payload.data);
        // state.cacheLastUpdate[payload.name] = payload.updateDate; 
    },
    updateCache: (state, payload) => {
        var index = state.theCache[payload.name].findIndex(x => x.id === payload.data.id); // not supported in IE
        if (index >= 0)
            state.theCache[payload.name][index] = payload.data;
        // state.cacheLastUpdate[payload.name] = payload.updateDate; 
    },
    updateCacheByIndex: (state, payload) => { 
        state.theCache[payload.name][payload.index] = payload.data;
    },
    deleteCacheByIndex: (state, payload) => { 
        state.theCache[payload.name].splice(payload.index, 1);
    },

    setUpdated: (state, payload) => {
        state.cacheLastUpdate[payload.name] = payload.updateDate;
    },
    setActivOnArrival: (state, payload) => {
        state.activeOnArrival.list = payload.list;
        state.activeOnArrival.id = payload.id;
    },
    resetActivOnArrival: (state) => {
        state.activeOnArrival.list = '';
        state.activeOnArrival.id = '';
    },
    setTheme: (state, payload) => {
        state.styleTheme = payload.name;
    },
};

const actions = {
    resetActiveOnArrival: (context) => {
        context.commit('resetActivOnArrival');
    },
    setActiveOnArrival: (context, payload) => {
        if (payload !== null && payload !== undefined) { 
            context.commit('setActivOnArrival', payload);
        }
    },
    setStyleTheme: (context, payload) => {
        if (payload !== null && payload !== undefined) { 
            context.commit('setTheme', payload);
        }
    },

    cacheReset: (context, name) => {
        context.commit('resetCache', name);
    },
    cacheFill: (context, payload) => {
        if (payload !== null && payload !== undefined) {
            context.commit('fillCache', payload);
            context.commit('setUpdated', payload);
            console.log('Caching: ' + payload.name);
        }
    
    },
    cacheAddItem: (context, payload) => {
        if (payload !== null && payload !== undefined) {
            context.commit('addToCache', payload);
            context.commit('setUpdated', payload);
        }
        
    },
    cacheDeleteItem: (context, payload) => {
        if (payload !== null && payload !== undefined) {
            var i = state.theCache[payload.name].findIndex(x => x.id === payload.id); // not supported in IE
            if (i >= 0)
                context.commit('deleteCacheByIndex', {name: payload.name, index: i});
            context.commit('setUpdated', payload);
        }
    },
    cacheDeleteDDLItem: (context, payload) => {
        if (payload !== null && payload !== undefined) {
            var i = state.theCache[payload.name].findIndex(x => x.rowid === payload.id); // not supported in IE
            if (i >= 0)
                context.commit('deleteCacheByIndex', {name: payload.name, index: i});
            context.commit('setUpdated', payload);
        }
    },
    cacheUpdateDDLItem: (context, payload) => {
        if (payload !== null && payload !== undefined) {
            var i = state.theCache[payload.name].findIndex(x => x.rowid === payload.data.rowid); // not supported in IE
            if (i >= 0)
                context.commit('updateCacheByIndex', {name: payload.name, data: payload.data, index: i});
            context.commit('setUpdated', payload);
        }
    },
    cacheUpdateItem: (context, payload) => {     
        if (payload !== null && payload !== undefined) {
            context.commit('updateCache', payload);
            context.commit('setUpdated', payload);
        }
    },
    casheSetUpdatedDate(context, payload) {
        if (payload !== null && payload !== undefined)
            context.commit('setUpdated', payload);
    },
    cacheUpdate(context, payload) {
        console.log('Updating cache: ' + payload.name + ' / ' + payload.updateDate);
        // console.log(payload.data);
        for (const row of payload.data) {               // not supported in IE
            // console.log(row.id);
            var rowExists = false;
            for (var i = 0; i < context.state.theCache[payload.name].length; i++) {
                if (context.state.theCache[payload.name][i].id == row.id) {
                    if (row.rowDeleted == true) {
                        context.commit('deleteCacheByIndex', {name: payload.name, index: i});
                        console.log('Deleted row ' + row.id);
                    } else {
                        context.commit('updateCacheByIndex', {name: payload.name, data: row, index: i});
                        console.log('Updated row ' + row.id);
                    }
                    rowExists = true;
                    break;
                }
            }
            if (!rowExists && !row.rowDeleted) {
                console.log('Added row ' + row.id);
                context.commit('addToCache', {name: payload.name, data: row});
            }
        }
        context.commit('setUpdated', payload);
    },
};

export default {
    state,
    getters,
    mutations,
    actions
}