<template>
<!-- <main :class="{myLightTheme:true}"> -->
  <main>
  <router-view />
  </main>
  
</template>

<script>
import i18n from '@/plugins/i18n'
import store from  './store/index';
export default {

  

  name: 'App',
  methods: {
    
  },
  created() {
    //  debugger;
    const currentPath = this.$router.history.current.name
   
    const s = store;
    const lang = i18n.locale;
    // debugger;
    if (window.localStorage.getItem('authenticated') !== 'true' && currentPath !== "Register" ) {      
      this.$router.push({name :'Login' , params : {lang : lang}});
    }else if(currentPath === "Register") 
    {

    }    
    else {
      let l = lang;
      if(s.user){
        l = s.user.lang
      }
      this.$router.replace({name :'AnalyticsDashboard' , params : {lang : l}});
    }
  },
};
</script>

<style src="./styles/theme.scss" lang="scss" />
