
import axios from 'axios';
import router from  '../Routes'


export default {
namespaced: true,
state : {
    idToken: null,
    userId: null,
    user: null,
    role: null,
    lang : null,
    errorMessage: 'OK',
  },

  mutations: {
    // authUser (state, userData) {
    //   

    //   state.idToken = userData.token
    //   state.userId = userData.userId
    //   state.role = userData.role
    // },
    storeUser (state, user) {
      
      state.idToken = user.token
      state.userId = user.userId
      state.role = user.role
      state.lang = user.lang
      state.user = user
    },

    lang (state,lang){
      debugger;
      state.lang = lang;
    },

    clearAuthData (state) {
      state.idToken = null
      state.userId = null
      state.user = null
      state.role = null
      state.lang = null
    }
  },

  actions: {
    setLogoutTimer ({commit, dispatch}, expirationTime) {
      setTimeout(() => {
        // commit('logout')
        localStorage.setItem('errorMessage', 'sessionExpired');
        dispatch('logout');

        // this.logout;
      }, expirationTime * 1000)
    },
    signup ({commit, dispatch}, authData) {
      axios.post('/Accounts', {
        email: authData.email,
        password: authData.password,
        returnSecureToken: true
      })
        .then(res => {
         
          commit('authUser', {
            token: res.data.idToken,
            userId: res.data.localId
          })
          const now = new Date()
          const expirationDate = new Date(now.getTime() + res.data.expiresIn * 1000)
          localStorage.setItem('token', res.data.idToken)
          localStorage.setItem('userId', res.data.localId)
          localStorage.setItem('expirationDate', expirationDate)
          dispatch('storeUser', authData)
          dispatch('setLogoutTimer', res.data.expiresIn)
        })
        // eslint-disable-next-line no-console
        .catch(error => { console.log(error)})
    },
    async login ({commit, dispatch}, authData) {
      // eslint-disable-next-line no-debugger
      axios.post('/Auth/login', {
        UserName: authData.email,
        Password: authData.password,
       // returnSecureToken: true        
      })
        .then(res => {
          // eslint-disable-next-line no-debugger
         // eslint-disable-next-line no-console
         console.log(res)
          const now = new Date()
          
          const expirationDate = new Date(now.getTime() + res.data.accessToken.expiresIn * 1000)
          localStorage.setItem('token', res.data.accessToken.token)          
          localStorage.setItem('expirationDate', expirationDate)
          localStorage.setItem('authenticated', true);
          localStorage.setItem('lang', res.data.lang);
          localStorage.setItem('errorMessage', 'OK');
          // commit('authUser', {
          //   token: res.data.accessToken.token,
          //   userId: "",
          //   role : res.data.role

          // });
          commit('storeUser', {
            token: res.data.accessToken.token,
            userId: "",
            role : res.data.role,
            lang : res.data.lang
           })
           
          // dispatch('setLogoutTimer', res.data.accessToken.expiresIn);
          // dispatch('setLogoutTimer', 120);
          router.push({name :'AnalyticsDashboard' , params : {lang : res.data.lang}});
        })
       // eslint-disable-next-line no-console
       .catch(error => {localStorage.setItem('errorMessage', 'badLogin');console.log(error); /*Messenger().post({ message: "Login lub haso s niepoprawne, prosz sprbowa ponownie&hellip;", type: "error" , showCloseButton: true}); */})
    },
    tryAutoLogin ({commit}) {
      const token = localStorage.getItem('token')
      if (!token) {
        return
      }
      const expirationDate = localStorage.getItem('expirationDate')
      const now = new Date()
      if (now >= expirationDate) {
        return
      }
      const userId = localStorage.getItem('userId')
      commit('authUser', {
        token: token,
        userId: userId
      })
    },
    logout ({commit}) {
      
      commit('clearAuthData')
      const lang = localStorage.getItem('lang')
      localStorage.removeItem('expirationDate')
      localStorage.removeItem('token')
      localStorage.removeItem('userId')
      localStorage.setItem('authenticated', false);
      localStorage.removeItem('lang');
      router.replace({name :'Login' , params : {lang : lang}})
    },
    // eslint-disable-next-line no-unused-vars
    storeUser ({commit, state}, userData) {
      if (!state.idToken) {
        return
      }
      axios.post('/users.json' + '?auth=' + state.idToken, userData)
        // eslint-disable-next-line no-console
        .then(res => console.log(res))
        // eslint-disable-next-line no-console
        .catch(error => console.log(error))
    },
    fetchUser ({commit, state}) {
      
      if (!state.idToken) {
        return
      }
      axios.get('/users.json' + '?auth=' + state.idToken)
        .then(res => {
          // eslint-disable-next-line no-console
          console.log(res)
          const data = res.data
          const users = []
          for (let key in data) {
            const user = data[key]
            user.id = key
            users.push(user)
          }
         // eslint-disable-next-line no-console
         console.log(users)
          commit('storeUser', users[0])
        })
        // eslint-disable-next-line no-console
        .catch(error => console.log(error))
    },
    changeUserLang({commit},lang){     
      commit('lang',lang);
    }
  },
 getters  : {
    user (state) {
      return state.user
    },
    isAuthenticated (state) {
      return state.idToken !== null
    }
  }

}